.square {
  background-color: #b0bcca;
  border-radius: 10px;
  clip-path: polygon(
    36% 0,
    0 0,
    0 29%,
    0 57%,
    0 100%,
    30% 100%,
    73% 100%,
    100% 100%,
    100% 59%,
    100% 31%,
    100% 0,
    74% 0
  );
  height: 100px;
  margin: 5px;
  transition: all 0.2s;
  width: 100px;
}

.square--X {
  background-color: #f7951d;
  clip-path: polygon(
    20% 0,
    0 20%,
    30% 50%,
    0 80%,
    20% 100%,
    50% 70%,
    80% 100%,
    100% 80%,
    70% 50%,
    100% 20%,
    80% 0,
    50% 30%
  );
}

.square--O {
  background-color: #5564af;
  border-radius: 50%;
}

.square--X.winner {
  animation: x-winner-animation infinite 1s ease-in-out;
}

@keyframes x-winner-animation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.square--O.winner {
  animation: o-winner-animation infinite 1s ease-in-out;
}

@keyframes o-winner-animation {
  0% {
    border-radius: 50%;
  }
  50% {
    border-radius: 10px;
  }
  100% {
    border-radius: 50%;
  }
}
